<template>

  <section class="payplugUiBlock generalBlock -subscribe -center"  data-e2e-name="generalOauthLogin">
    <BlockHeader :blockHeaderTitle="title" :blockHeaderDescription="description" />
    <div class="_content">
      <Paragraph :paragraphText="label" />
      <div v-if="link_portal" class="_buttons">
        <ButtonLink
            class="payplugUIButton _connexion"
            :buttonLinkHref="link_portal.url"
            :buttonLinkText="link_portal.text"
            :buttonLinkTitle="link_portal.text"
            buttonLinkTarget="_self"
            data-e2e-name="payplugConnexion"
        />
        <PayplugButton
            v-bind:class = "(loading)?'-disabled':''"
            class="payplugUIButton _subscribe -tertiary"
            :buttonText="already_have_account"
            @click="showRegistration"
            data-e2e-name="payplugShowRegistration"/>
      </div>
    </div>
  </section>
</template>

<script>
import PayplugButton from "../atoms/AtomButton";
import Paragraph from "../atoms/AtomParagraph";
import ButtonLink from "../atoms/AtomButtonLink";
import BlockHeader from "../molecules/MoleculeBlockHeader";
import {store} from "@/store/store";

export default {
  name: "OrganismSectionOauthLogin",
  components:{ ButtonLink, Paragraph, PayplugButton, BlockHeader },
  computed:{
    title: {
      get: () => store.translations.oauth_login.title
    },
    description: {
      get: () => store.translations.oauth_login.description
    },
    already_have_account: {
      get: () => store.translations.oauth_login.already_have_account
    },
    label: {
      get: () => store.translations.oauth_login.content_description
    },
    link_portal:{
      get: () => store.translations.oauth_login.link_portal
    }
  },
  methods: {
    showRegistration(){
      this.$emit('notRegisted', true)
    },
    showLoggedIn(){
      this.$emit('showLoggedIn', true)
    },
  }
}
</script>

